}

.shadow {
  background-color: #5268b1;
}


@media (max-width: 768px) {

  .pokedex {
    width: 100%;
        padding: 10px 10px;
    box-sizing: border-box;
  }

  .pokedex__item {
    justify-content: stretch;
  }

  .pokedex__layout {
        flex-direction: column;
  }

  .pokedex__left {
    order: 1;
    width: 100%;
  }

  .pokedex__right {
    width: 100%;
    margin-bottom: 20px;
    padding-left: 0px;
  }

  .pokedex__list {
    justify-content: space-between;
  }

  .pokedex__card {
  position: relative;
    }

    table {
          width: 100%;
          font-size: 15px;
    }

.card__layout {
      display: flex;
}

.card__left {
  width: 40%;
}

.card__right {
  width: 60%;
  padding-left: 20px;
}

.card__image {
    width: 100%;
}



.types {
    flex-direction: column;
}

.types__item {
    margin-bottom: 5px;
  }

  }
